/* Input and select feild styles */
.payslip-inputs form {
  width: 83vw;
  height: 20vh;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}
.payslip-inputs select,
.payslip-inputs input {
  color: #1d3557;
  border: 0;
  background: none;
  border-bottom: 1px solid #1d3557;
  font-size: 18px;
  width: 20vw;
  padding-left: 1vw;
  height: 5vh;
  border-radius: 0.5vw;
}
.payslip-inputs input:focus,
.payslip-inputs select:focus {
  outline: none;
  box-shadow: 2px 2px 2px 2px #457b9d;
  transition: 0.5s ease-in-out;
}
.btn-create {
  padding: 20px;
  cursor: pointer;
  border: none;
  outline: none;
  background-color: #cedbee;
  border-radius: 10px;
}
.btn-create:hover{
  background-color: #1d3557;
  color: #cedbee;
  letter-spacing: .5px;
  transition: 0.2s ease-in-out 0.1s;
}
.payslip-data {
}
.card-panel.teal {
  padding: 50px 80px;
  display: flex;
}

.payslip-inputs {
  display: flex;
  justify-content: space-around;
  height: 20vh;
}

ul.collapsible {
  margin: 0 auto;
  width: 65%;
}

.payslip-inputs input:focus,
.payslip-inputs select:focus,
.select-date input:focus {
  outline: none;
  box-shadow: 2px 2px 2px 2px #457b9d;
  transition: 0.5s ease-in-out;
}
.select-date {
  width: 60vw;
  height: 30vh;
  position: relative;
  left: 13vw;
  top: 10vh;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  border: 4px #1d3557 solid;
  border-radius: 3vw;
  font-size: 25px;
  font-weight: bold;
}
.select-date div {
  height: 30vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
input[type='date'] {
  height: 5vh;
  width: 13vw;
  margin: 0vh 1vh;
  padding: 1vw;
  border-radius: 2vw;
  border: none;
  cursor: text;
}
.select-date button {
  color: #f1faee;
  text-decoration: none;
  background: #1d3557;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
  display: inline-block;
  border: none;
  transition: all 0.4s ease 0s;
}
.select-date button:hover {
  background: #999999;
  letter-spacing: 0.5px;
  transition: all 0.4s ease 0s;
}

.btn-openModal {
  background: #2c2c2c;
  color: #eee;
  font-size: 1rem;
  margin: 2rem;
  padding: 0.5rem 1.8rem;
  border: none;
  outline: none;
  cursor: pointer;
}

.modal-wrapper {
  background: #fff;
  width: 80%;
  margin: 4rem auto;
  transition: all 0.5s ease;
}

.modal-header {
  background: #1d3557;
  padding: 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.modal-header p,
span {
  color: white;
  font-size: 1.2rem;
}

.modal-header span {
  font-size: 1.5rem;
  cursor: pointer;
}

.modal-content {
  padding: 1.5rem 1rem;
}

.modal-body {
  margin-bottom: 1rem;
}

.modal-body h4 {
  font-size: 1.8rem;
  color: #dd501d;
}

.modal-body p {
  font-size: 1rem;
  background: #eee;
  color: #333;
  padding: 1rem;
  margin: 1rem 0 2rem 0;
}

.modal-footer button {
  display: block;
  font-size: 1rem;
  margin-left: auto;
  padding: 0.5rem 1.8rem;
  background: #dd501d;
  color: #fff;
  border: none;
  outline: none;
  cursor: pointer;
}

.personal-info,
.contract-info {
  width: 83.5vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 50px;
}

.personal-info h4,
.contract-info h4 {
  padding: 3vh;
  background-color: #1d3557;
  color: #f1faee;
  border-top-left-radius: 25px;
  border-bottom-right-radius: 25px;
  box-shadow: 2px 2px 2px 2px #333333;
  margin-bottom: 20px;
}
.personal-info div,
.contract-info div {
  display: flex;
  justify-content: space-evenly;
  width: 74vw;
  padding: 30px;
}

.personal-info p,
.contract-info p {
  border: none;
  box-shadow: 3px 7px 10px #1d3557;
  font-size: 18px;
  width: 20vw;
  padding: 10px;
  border-radius: 0.5vw;
  text-align: center;
  margin: 0;
}
.address-bar {
  width: 60vw !important;
}


.create-personal-info,
.create-contract-info{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  width: 83.5vw;
}
.create-personal-info div,
.create-contract-info div{
  width: 70vw;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
.create-personal-info div div{
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  width: 100%;
}
.create-personal-info div div div{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  margin-right: 20px;
}
.emp-img{
  width: 30vw!important;
  height: 23vh;
  align-self: center ;
  overflow: hidden;
  border: 1px #1d3557 solid;
  border-radius: 8vw;
  box-shadow:1px 3px 2px #1d3557;
}
.create-contract-info div div{
  display: flex;
  flex-direction: row;
  justify-content: center;
  
  margin-right: 10px;
}
.address-bar {
  width:100%!important;
  margin: 0px 5px;
}
.submit-button {
  width: 83.5vw;
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
}
.submit-button button {
  cursor: pointer;
  /* padding: 10px 25px; */
  background-color: #797979;
  border: 1px #1d3557 solid;
  border-radius: 0.5vw;
  color: #f1faee;  font-size: 20px;
}
.submit-button button:nth-of-type(1) {
  margin-right: 20px;
  background-color: #2e4e7a;
}
.submit-button button:hover{
  background-color: #1d3557;
  transition: all 0.2 ease-in-out;
} 
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
  text-decoration: none;
  color: #000000;
}
body {
  background: #F1FAEE;
}
.container {
  display: flex;
}
.text-align {
  align-items: center;
  display: flex;
  font-size: 1.2rem;
  justify-content: center;
  border-bottom: 1px #F1FAEE solid;
}

.sidebar {
  position: fixed;
  width: 15vw;
  border-right: 1px solid #f1faee;
  height: 100vh;
  background: #1d3557;
}

.date-info {
  height: 10vh;
  color: #f1faee;
}

.app-options {
  margin-top: 60px;
  width: 100%;
}

.app-options-group {
  display: flex;
  padding: 20px;
  font-size: 16px;
  width: 100%;
  justify-content: flex-start;
}
.app-options-group svg {
  fill: white;
  font-size: 20px;
}

.app-options-group div {
  padding: 0 10px;
  cursor: pointer;
  color: #f1faee;
}

.app-options-group:hover {
  background-color: #8d9097;
  border-radius: 50px;
  width: inherit;
  cursor: pointer;
  transition: all 100ms ease-in;
}

.main-content {
  display: flex;
  flex-direction: column;
}

.navbar {
  position: fixed;
  width: 85vw;
  height: 10vh;
  margin-left: 15vw;
  z-index: 4;
  background: #1d3557;
  color: #f1faee;
}
.iconBtn {
  align-self: center;
  display: flex;
  justify-content: space-between;
  padding: 0 25px 0 5px;
}
.iconBtn div {
  color: white;
  cursor: pointer;
}
.iconBtn svg {
  align-self: center;
  fill: white;
}
.main {
  width: 80vw;
  margin-top: 10vh;
  margin-left: 15vw;
}
@media screen and (max-width: 700px) {
  .app-options-group {
    pointer-events: none;
    display: flex;
    justify-content: center;
    border: none;
  }
  .app-options-group div:nth-of-type(2) {
    display: none;
  }
  .text-align {
    font-size: 0.8rem;
  }
}

